.contacts {
    &__head {
        h2 {
            font-size: 50px;
            font-weight: 700;
            color: rgb(110, 193, 228);
        }
    }
}

.forms {
    &__box {
        display: flex;
        flex-direction: column;
        input {
            border: 1px solid #CCCCCC;
            padding: 5px;
            border-radius: 3px;
            outline: none;
        }
        label {
            font-weight: bold;
            font-size: 18px;
            span {
                color: red;
            }
        }
    }
    &__radio--button {
        input {
            margin-top: 10px;
        }
        label {
            font-size: 18px;
            font-weight: normal;
        }
    }
    textarea {
        padding: 10px;
        width: 30vw;
        border-radius: 3px;
        border: 1px solid #CCCCCC;
        outline: none;
    }
    button {
        width: 90px;
        padding: 10px;
        border: 1px solid #CCCCCC;
        outline: none;
        font-weight: bold;
        margin-bottom: 20px;
    }
}

@media (max-width: 768px) {
    .forms {
        textarea {
            width: 100%;
        }
    }
    .contacts {
        &__head {
            h2 {
                font-size: 40px;
            }
        }
    }
}