    .about {
        &__main {
            img {
                width: 100%;
            }
        }
        &__inner {
            h1 {
                text-align: center;
                color: #6EC1E4;
                font-size: 50px;
                font-weight: 700;
            }
        }
        &__texts {
            p {
                color: #7A7A7A;
                margin-top: 10px;
                font-size: 18px;
            }
            img {
                width: 100%;
            }
        }
    }
    
    //media query style
    @media (max-width: 767px) {
        .about {
            &__inner {
                h1 {
                    font-size: 30px;
                }
            }
        }
    }