.privacy {
    &__head {
        h2 {
            color: rgb(110, 193, 228);
            text-align: center;
            font-size: 50px;
        }
        &__inner {
            p {
                font-size: 18px;
            }
        }
    }
}

// media query style
@media (max-width: 330px) {
    .privacy {
        &__head {
            h2 {
                font-size: 39px;
                margin-top: 20px;
                margin-bottom: 20px;
            }
        }
    }
}