            // foooter css style starts
            footer {
                background-color: #41CAF8;
            }
            
            .footer {
                &__main {
                    color: #ededed;
                }
                &__inner {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                }
                &__name {
                    font-size: 20px;
                }
                &__text {
                    margin-left: 25px;
                    p {
                        font-size: 20px;
                        a {
                            text-decoration: none;
                            color: #fff;
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
            
            // media query style
            @media (max-width: 767.99px) {
                .footer {
                    &__inner {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                    }
                    &__name {
                        font-size: 18px;
                    }
                    &__text {
                        margin-left: 0px;
                        p {
                            font-size: 16px;
                        }
                    }
                }
            }