    // starts content head cards css style
    .content {
        &__etiket {
            position: absolute;
            right: 0;
            z-index: 1;
            width: 100px;
            p {
                background-color: #FF9900;
                color: white;
                font-size: 13;
                text-transform: uppercase;
                margin-top: 39px;
                width: 100%;
                line-height: 1.5;
                text-align: center;
                transform: translateY(-35%) translateX(-35%) translateX(30px) rotate(45deg);
            }
        }
        &__box-item {
            position: relative;
            a {
                text-decoration: none;
            }
        }
        &__box-item:hover {
            img {
                transform: scale(1.1);
                background: rgba(0, 0, 0, 0.5, );
                opacity: 1.5;
            }
        }
        &__cards-head {
            text-align: center;
            h2 {
                color: #6EC1E4;
                font-size: 50px;
                font-weight: 600;
            }
            h3 {
                font-size: 24px;
                color: #7A7A7A;
                font-weight: 500;
            }
        }
        // end styles cards css style contents head
        &__inner-new {
            overflow: hidden;
            img {
                width: 100%;
                object-fit: cover;
                opacity: 1;
                transition: 1.5s ease-in;
            }
        }
        &__inner-text {
            background-color: #ededed;
            color: #55595c;
            text-align: center;
            padding: 10px 55px;
            min-height: 220px;
            span {
                border: 2px solid #55595c;
                color: #55595c;
                border-radius: 3px;
                padding: 7px 15px;
            }
            h2 {
                font-weight: 645;
                margin-top: 15px;
                font-size: 23px;
                margin-top: 30px;
            }
            p {
                margin-top: 15px;
                font-size: 17px;
                padding: 0 15px;
                margin-bottom: 15px;
            }
        }
    }
    
    // slider content scss style start
    .slider {
        position: relative;
        .slick-dots {
            position: absolute;
            bottom: 3px;
            display: block;
            width: 100%;
            padding: 0;
            margin: 0;
            list-style: none;
            text-align: center;
        }
        .slick-prev {
            left: 19px;
            color: transparent;
            z-index: 1;
            background-color: transparent;
        }
        .slick-next {
            right: 19px;
            z-index: 1;
            color: transparent;
            background-color: transparent;
        }
    }
    
    //  aside menu styles start
    .aside {
        &-close__btn {
            position: absolute;
            right: 4px;
            padding: 3px 15px;
            border: 1px solid black;
        }
        &__menu {
            display: none;
            background-color: white;
            position: absolute;
            box-shadow: 2px 2px 4px 4px rgb(151, 151, 151);
            position: fixed;
            top: 0;
            height: 100vh;
            z-index: 999;
            width: 45%;
            overflow-y: auto;
        }
        &__list {
            ul {
                padding-left: 8px;
            }
            li {
                list-style-type: none;
                margin-top: 15px;
                a {
                    text-decoration: none;
                    color: black;
                    font-weight: bold;
                }
            }
        }
    }
    
    // jquery add class
    .active-menu {
        display: block;
    }
    
    // jquery add class
    // --------------------
    //  aside menu styles end
    //  media query style
    @media (max-width: 991px) {
        .content {
            &__inner-text {
                padding: 10px 25px;
                h2 {
                    margin-top: 15px;
                    font-size: 20px;
                }
                p {
                    font-size: 15px;
                }
            }
        }
    }
    
    @media (max-width: 767px) {
        .content {
            &__inner-text {
                padding: 10px 25px;
                h2 {
                    margin-top: 15px;
                    font-size: 20px;
                }
                p {
                    font-size: 15px;
                    margin-bottom: 30px;
                }
            }
        }
    }
    
    @media (max-width: 525px) {
        .aside {
            &__menu {
                width: 70%;
            }
        }
        .content {
            &__cards-head {
                h2 {
                    font-size: 39px;
                }
            }
        }
        @media (max-width: 405px) {
            .aside {
                &__menu {
                    width: 100%;
                }
            }
            .content {
                &__cards-head {
                    h2 {
                        margin-top: 10px;
                        font-size: 29px;
                    }
                    h3 {
                        font-size: 18px;
                        margin-top: 10px;
                    }
                }
            }
        }
    }