            .details {
                &__main {
                    background-color: #EDEDED;
                }
                &__inner {
                    h4 {
                        color: rgb(122, 122, 122);
                        font-weight: 700;
                    }
                    p {
                        color: rgb(122, 122, 122);
                        font-size: 19px;
                        font-weight: 400;
                    }
                }
                &__texts {
                    p {
                        font-size: 19px;
                        font-weight: 400;
                    }
                }
                &__video {
                    iframe {
                        max-width: 100%;
                        // height mediada ele 320
                    }
                }
                // app google section
                &__box {
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    img {
                        width: 100%;
                    }
                }
                &__appstore {
                    max-width: 150px;
                    max-height: 50px;
                }
                &__googleplay {
                    max-width: 150px;
                    max-height: 50px;
                    margin-left: 20px;
                }
                &__amazon {
                    max-width: 150px;
                    max-height: 50px;
                    margin-left: 20px;
                }
                &__app {
                    h2 {
                        color: #6EC1E4;
                        font-size: 50px;
                    }
                }
            }
            
            .slider__item {
                width: 232px !important;
                height: 200px !important;
                margin-right: 10px;
                img {
                    width: 100% !important;
                    height: 200px !important;
                }
            }
            
            .slick-next {
                right: 15px !important;
                z-index: 1;
            }
            
            .slick-prev {
                left: 15px !important;
                z-index: 1;
            }
            
            //media query style
            @media (max-width: 767px) {
                .details {
                    &__photo {
                        text-align: center;
                    }
                    &__inner {
                        h4 {
                            margin-top: 20px;
                        }
                    }
                    &__video {
                        iframe {
                            height: 320px;
                        }
                    }
                }
            }
            
            @media (max-width: 360px) {
                .slick-dots {
                    display: none !important;
                }
            }